@charset "UTF-8";
:root {
  --base-font: "Zen Kaku Gothic New", sans-serif;
  --anton-font: "Anton", sans-serif;
  --roboto-font: "Roboto", sans-serif;
}

:root {
  --light: 300;
  --normal: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
}

:root {
  --black: #333;
  --white: #fff;
  --main: #00561F;
  --light-green: #A5D4AD;
  --light-beige: #FAF2EE;
  --light-gray: #E8E8E8;
  --gray: #707070;
  --gray2: #D7D7D7;
  --dark-blue: #003567;
  --link: #4182B4;
  --bg-light-gray: #F5F5F5;
  --bg-drawer: #F7F7F7;
  --bg-light-green: #EBF6EF;
  --bg-light-blue: #E1EAFA;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6vw;
  }
}
@media (min-width: 1000px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

/* hover指定できるPCを想定したスタイル */
@media (hover: hover) {
  a:hover {
    opacity: 0.7;
  }
}
/* hoverが使えないタッチ端末を想定した装飾 */
@media (hover: none) {
  a:active {
    opacity: 0.7;
  }
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100svh;
  text-rendering: optimizeSpeed;
  font-size: 1.125rem;
  font-weight: var(--medium);
  font-family: var(--base-font);
  color: var(--black);
  line-height: 1.8;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
@media screen and (min-width: 768px) {
  body {
    font-size: 1rem;
    line-height: 2;
  }
}

body.is-fixed {
  overflow: hidden;
}

main {
  flex: 1;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
/* Set core body defaults */
body {
  min-height: 100svh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

